<template>
  <div class="play">
    <div class="main">
      <div class="zxc">
        <ul
          @click="showTime = !showTime"
          ref="srcoll"
          :style="{ opacity: lightOpacity }"
        >
          <li v-for="item in parseInt(own[index].end_var)" :key="item">
            <img
              :src="
                'https://mhpic.xiaomingtaiji.net/comic/' +
                own[index].chapter_image[Definition].replace('$$', item)
              "
              @error="this.src = errorimg"
            />
          </li>
        </ul>
      </div>
      <div class="menu">
        <transition
          name="custom-classes-transition"
          enter-active-class="animate__animated animate__slideInDown"
          leave-active-class="animate__animated animate__slideOutUp"
        >
          <div class="topnav" v-if="showTime">
            <span @click="back">三( ᐛ )</span>
            <span>{{ own[index].chapter_name }}</span>
            <van-popover
              v-model="showPopover"
              theme="dark"
              trigger="click"
              :actions="actions"
              placement="bottom-end"
              @select="select"
            >
              <template #reference>
                <van-button type="primary">···</van-button>
              </template>
            </van-popover>
          </div>
        </transition>
        <transition
          enter-active-class="animate__animated animate__slideInUp"
          leave-active-class="animate__animated animate__slideOutDown"
        >
          <div class="bottomnav" v-if="showTime">
            <div class="meter" v-if="meterFind">
              <div class="last">
                <van-icon name="arrow-left" @click="lastOwn" />
              </div>
              <div class="mete">
                <van-slider
                  v-model="value"
                  :min="0"
                  :max="own[index].end_var - 1"
                  :step="1"
                  button-size="12px"
                  active-color="#fb6454"
                  inactive-color="rgba(255,255,255,0.3)"
                />
              </div>
              <div class="next">
                <span>{{ value + 1 }}/{{ own[index].end_var }}</span>
                <van-icon name="arrow" @click="nextOwn" />
              </div>
            </div>
            <div class="meter" v-if="!meterFind">
              <div class="last">
                <van-icon
                  name="minus"
                  @click.stop="
                    rollingSpeed <= 1
                      ? (rollingSpeed = 1)
                      : (rollingSpeed -= 0.1)
                  "
                />
              </div>
              <div class="mete">
                <van-slider
                  v-model="rollingSpeed"
                  :min="1"
                  :max="5"
                  :step="0.1"
                  button-size="12px"
                  active-color="#fb6454"
                  inactive-color="rgba(255,255,255,0.3)"
                />
              </div>
              <div class="next">
                <van-icon
                  name="plus"
                  @click.stop="
                    rollingSpeed >= 5
                      ? (rollingSpeed = 5)
                      : (rollingSpeed += 0.1)
                  "
                />
              </div>
            </div>
            <div class="fivegood">
              <div class="num">
                <van-cell @click="showPopup"
                  ><van-icon name="setting-o" />设置</van-cell
                >
              </div>
              <div class="num">
                <van-icon
                  name="pause-circle-o"
                  @click="meterFind = !meterFind"
                  v-show="!meterFind"
                />
                <van-icon
                  name="play-circle-o"
                  @click="meterFind = !meterFind"
                  v-show="meterFind"
                />自动
              </div>
              <div class="num">
                <van-cell @click="showeyeup"
                  ><van-icon name="closed-eye" />画质</van-cell
                >
              </div>
              <div class="num">
                <!-- <van-icon name="bulb-o" />亮度 -->
                <van-cell @click="showlightup"
                  ><van-icon name="bulb-o" />亮度</van-cell
                >
              </div>
              <div class="num">
                <!-- <van-icon name="todo-list-o" />目录 -->
                <van-cell @click="showlistup"
                  ><van-icon name="todo-list-o" />目录</van-cell
                >
              </div>
            </div>
          </div>
        </transition>

        <!-- 全局功能弹出层 -->
        <div class="waimian">
          <van-popup
            v-model="show"
            position="bottom"
            :style="{ height: '20%' }"
          >
            <div class="fourFind">
              <div class="find"><p>右手翻页</p></div>
              <div class="find"><p>左手翻页</p></div>
              <div class="find">
                <p :class="{ nowLook: nowLook }">竖屏卷轴</p>
              </div>
              <div class="find"><p>横屏卷轴</p></div>
            </div>
            <div class="p"><p>更多设置</p></div>
          </van-popup>

          <div class="selectLook">
            <van-popup v-model="eyeShow">
              <van-tabs v-model="active" @click="lookDefinition">
                <van-tab title="流畅"></van-tab>
                <van-tab title="标清"></van-tab>
                <van-tab title="高清"></van-tab>
              </van-tabs>
            </van-popup>
          </div>

          <van-popup
            v-model="listShow"
            position="right"
            :style="{ height: '100%', width: '32%' }"
          >
            <div class="rightlist">
              <div class="tops">
                <div class="bos" @click="back">
                  <van-icon name="apps-o" />全集
                </div>
                <div class="bos" @click="toggle">
                  <van-icon name="arrow-up" v-show="arrow" class="icontop" />
                  <van-icon
                    name="arrow-down"
                    v-show="!arrow"
                    class="iconbottom"
                  />排序
                </div>
              </div>
              <div class="nowlist">
                <ul>
                  <li
                    v-for="(item, Nowindex) in own"
                    :key="Nowindex"
                    :class="{ lightli: Nowindex == index }"
                    @click="chooseIndex(Nowindex)"
                  >
                    <div class="imgs">
                      <img
                        v-lazy="
                          'https://mhpic.xiaomingtaiji.net/comic/' +
                          own[Nowindex].chapter_image.high.replace('$$', '1')
                        "
                      />
                    </div>
                    <p>{{ item.chapter_name }} {{ item.chapter_title }}</p>
                  </li>
                </ul>
              </div>
              <div class="bottoms">
                <div class="bos"><van-icon name="add-o" />添加</div>
                <div class="bos"><van-icon name="label-o" />书签</div>
              </div>
            </div>
          </van-popup>

          <div class="lightopacity">
            <!-- <div class="big" :style="{opacity:lightOpacity}"></div> -->
            <van-popup v-model="showlight">
              <van-slider
                v-model="lightOpacity"
                :min="0.2"
                :max="1"
                :step="0.01"
                :bar-height="'3px'"
                :button-size="'15px'"
                :active-color="'#e1594b'"
                :inactive-color="'rgba(255, 255, 255, 0.2)'"
              >
                <!-- <template #button>
                  <div class="custom-button">{{ lightOpacity.toFixed(2) }}</div>
                </template> -->
              </van-slider>
              <div class="chooselight">
                <div
                  class="one"
                  @click="(lightOpacity = 1), (one = true)"
                  :class="{ two: one }"
                >
                  <van-icon name="circle" v-if="!one" />
                  <van-icon name="passed" v-else />默认亮度
                </div>
                <div
                  class="one"
                  @click="(lightOpacity = 0.52), (two = true)"
                  :class="{ two: two }"
                >
                  <van-icon name="circle" v-if="!two" />
                  <van-icon name="passed" v-else />夜间模式
                </div>
              </div>
            </van-popup>
          </div>
          <div class="share">
            <van-popup v-model="share">
              <div class="top">
                <img src="https://m.zymk.cn/static/images/default/river/baby-hj1.png" alt="">
                <div class="tips" @click="vantToast(0)">分享漫画</div>
                <p>漫画无限好，分享不能少</p>
              </div>
              <div class="one" @click="vantToast(1)">
                <div class="img"></div>
                微博
              </div>
              <div class="two" @click="vantToast(2)">
                <div class="img"></div>
                腾讯
              </div>
              <div class="three" @click="vantToast(3)">
                <div class="img"></div>
                微信
              </div>
            </van-popup>
          </div>
        </div>
        <div class="help" v-if="helpShow" @click="helpShow = false">
          <div class="last"><p>上一页</p></div>
          <div class="next"><p>下一页</p></div>
          <div class="center"><p>帮助</p></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import errorimg from "../assets/error.jpg";
import { Toast } from "vant";

export default {
  data() {
    return {
      helpShow: false,
      timer: null,
      rollingSpeed: 1,
      meterFind: true,
      one: false,
      two: false,
      errorimg: errorimg,
      Definition: "high",
      active: 2,
      own: "",
      lightOpacity: 1,
      share: false,
      showlight: false,
      nowLook: true,
      listShow: false,
      eyeShow: false,
      show: false,
      index: 0,
      showPopover: false,
      // 通过 actions 属性来定义菜单选项
      actions: [
        { text: "首页" },
        { text: "分享" },
        { text: "反馈" },
        { text: "帮助" },
      ],
      value: 0,
      showTime: false,
      move: false,
      scrollTopMath: 0,
    };
  },
  mounted() {
    window.addEventListener("scroll", this.onSroll);
  },
  created() {
    this.$store.commit("falseFooterFlag");
    this.own = this.$route.params.own;
    this.index = this.$route.params.index;
    this.value = 0;
    window.addEventListener("scroll", this.onSroll);
    // window.onscroll = () => {
    //   var listLis = document.querySelectorAll(".main ul li");
    //   this.scrollTopMath = document.documentElement.scrollTop;
    //   var clientHeight = document.documentElement.clientHeight;
    //   var clientHeightAll = 0;
    //   // var b = 0;
    //   window.addEventListener('scroll', this.onSroll)
    // };
  },
  destroyed() {
    this.meterFind = true;
    this.showTime = false;
    window.removeEventListener("scroll", this.onSroll, false);
  },
  activated() {
    this.$store.commit("falseFooterFlag");
    // this.own = this.$route.params.own;
    // this.index = this.$route.params.index;
    this.value = 0;
    window.addEventListener("scroll", this.onSroll);
    console.log(this.own);
  },
  beforeRouteUpdate(to, from, next) {
    this.$store.commit("falseFooterFlag");
    // this.own = this.$route.params.own;
    // this.index = this.$route.params.index;
    this.value = 0;
    window.addEventListener("scroll", this.onSroll);

    // window.onscroll = () => {
    //   var listLis = document.querySelectorAll(".main ul li");
    //   this.scrollTopMath = document.documentElement.scrollTop;
    //   var clientHeightAll = 0;
    //   var b = 0;

    //   for (var i = 1; i <= listLis.length; i++) {
    //     clientHeightAll += listLis[i - 1].clientHeight;
    //     if (this.scrollTopMath + 10 > clientHeightAll) {
    //       b = i;
    //     }
    //   }
    //   this.value = b;
    // };
    next();
  },
  watch: {
    value() {
      var listLi = document.querySelectorAll(".main ul li");
      var listLiTop = 0;
      for (var i = 1; i <= this.value; i++) {
        if (this.value == 0) {
          break;
        } else {
          listLiTop += listLi[i - 1].clientHeight;
        }
      }
      window.scrollTo(0, listLiTop);
    },
    own() {
      this.own = this.$route.params.own;
    },
    arrow() {
      this.own.reverse();
    },
    lightOpacity() {
      if (this.lightOpacity != 0) {
        this.one = false;
      }
      if (this.lightOpacity != 0.6) {
        this.two = false;
      }
    },
    meterFind() {
      if (!this.meterFind) {
        this.setInterval();
      } else {
        clearInterval(this.timer);
      }
    },
  },
  methods: {
    vantToast(s){
      if(s==0){
        this.helpShow = false;
        this.$toast('请点击下列分享方式');
      }else if(s==1){
        this.helpShow = false;
        this.$toast.fail('请安装微博哦');
      }else if(s==2){
        this.helpShow = false;
        this.$toast.fail('请安装QQ哦');
      }else if(s==3){
        this.helpShow = false;
        this.$toast.fail('请安装微信哦');
      }
    },
    setInterval() {
      this.timer = setInterval(() => {
        window.scrollTo(0, (this.scrollTopMath += this.rollingSpeed));
      }, 10);
    },
    onSroll() {
      var listLis = document.querySelectorAll(".main ul li");
      this.scrollTopMath = document.documentElement.scrollTop;
      // if(document.documentElement.clientHeight==undefined){
      //   var clientHeight = 0 ;
      // }else{
      var clientHeight = document.documentElement.clientHeight;
      // }
      var clientHeightAll = 0;
      var b = 0;

      for (var i = 1; i <= listLis.length; i++) {
        clientHeightAll += listLis[i - 1].clientHeight;
        if (this.scrollTopMath + 10 > clientHeightAll) {
          b = i;
        }
      }
      if (
        clientHeightAll - clientHeight <=
        Math.floor(this.scrollTopMath + 2)
      ) {
        this.meterFind = true;
        b = this.own[this.index].end_var - 1;
        this.showTime = true;
      } else if (Math.floor(this.scrollTopMath) == 0) {
        this.showTime = true;
      }
      this.value = b;
    },
    lookDefinition(s) {
      if (s == 0) {
        this.Definition = "low";
      } else if (s == 1) {
        this.Definition = "middle";
      } else if (s == 2) {
        this.Definition = "high";
      }
      this.eyeShow = false;
    },
    showlightup() {
      this.showTime = false;
      this.showlight = true;
    },
    showlistup() {
      this.listShow = true;
      this.showTime = false;
    },
    showeyeup() {
      this.eyeShow = true;
      this.showTime = false;
    },
    showPopup() {
      this.show = true;
      this.showTime = false;
    },
    onSelect(action) {
      Toast(action.text);
    },
    back() {
      this.meterFind = true;
      this.showTime = false;
      window.removeEventListener("scroll", this.onSroll, false);
      this.$router.go(-1);
    },
    lastOwn() {
      // this.showTime = false;
      if (this.arrow) {
        // console.log(1);
        if (this.value <= 0) {
          this.value = 0;
          if (this.index <= 0) {
            this.index = 0;
          } else {
            this.index = parseInt(this.index) - 1;
          }
        } else {
          this.value--;
        }
      } else {
        // console.log(2);
        if (this.value <= 0) {
          this.value = 0;
          if (this.index >= this.own.length) {
            this.index = this.own.length;
          } else {
            this.value = 0;
            this.index = parseInt(this.index) + 1;
          }
        } else {
          this.value--;
        }
      }
    },
    nextOwn() {
      // this.showTime = false;
      if (this.arrow) {
        // console.log(1);
        if (this.value >= this.own[this.index].end_var - 1) {
          this.value = this.own[this.index].end_var - 1;
          if (this.index >= this.own.length) {
            this.index = this.own.length;
          } else {
            this.value = 0;
            this.index = parseInt(this.index) + 1;
          }
        } else {
          this.value++;
        }
      } else {
        // console.log(2);
        if (this.value >= this.own[this.index].end_var - 1) {
          this.value = this.own[this.index].end_var - 1;
          if (this.index <= 0) {
            this.index = 0;
          } else {
            this.value = 0;
            this.index = parseInt(this.index) - 1;
          }
        } else {
          this.value++;
        }
      }
    },
    select(s) {
      if (s.text == "首页") {
        this.$router.push({ path: "/" });
      } else if (s.text == "分享") {
        this.showTime = false;
        this.share = true;
      } else if (s.text == "反馈") {
        this.$router.push({path:'/Feedback'});
      } else if (s.text == "帮助") {
        this.showTime = false;
        this.helpShow = true;
      }
    },
    toggle() {
      this.$store.commit("toggleList");
      this.index = this.own.length - 1 - this.index;
    },
    chooseIndex(n) {
      this.index = n;
    },
  },
  computed: {
    arrow() {
      return this.$store.state.arrow;
    },
  },
};
</script>

<style lang='less'>
.van-toast--text,.van-toast--fail{
  background: #fc6454;
}
.custom-button {
  width: 26px;
  color: #fff;
  font-size: 10px;
  line-height: 18px;
  text-align: center;
  background-color: #ee0a24;
  border-radius: 100px;
}
.play {
  .main {
    .zxc {
      background: rgba(0, 0, 0, 1);
      ul {
        font-size: 0;
      }
    }
  }
  .menu {
    .waimian {
      .share {
        .van-popup--center {
          width: 70%;
          // height: 250px;
          height: 50%;
          display: flex;
          justify-content: space-around;
          align-items: flex-end;
          border-radius: 10px;
          padding-bottom: 15px;
          .top{
            width: 100%;
            // height: 160px;
            height: calc(100% - 100px);
            border-radius: 0 0 50% 50%;
            background-color: #0f0f0f;
            position: absolute;
            top: 0;
            left: 0;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            .tips{
              width: 80%;
              height: 50px;
              background-color: #FF9462;
              position: absolute;
              top: 5%;
              border-radius: 40px;
              color: white;
              display: flex;
              justify-content: center;
              align-items: center;
            }
            p{
              position: absolute;
              top: 30%;
              font-size: 18px;
              color: white;
            }
            img{
              width: 80px;
              display: block;
              padding-bottom: 15px;
            }
          }
          .one{
            height: 50px;
            display: flex;
            align-items: flex-end;
            flex-wrap: wrap;
            width: 32px;
            .img{
              background: url('https://www.zymk.cn/static/images/default/share.png')no-repeat 0 -3px;
              width: 32px;
              height: 32px;
            }
          }
          .two{
            height: 50px;
            display: flex;
            align-items: flex-end;
            flex-wrap: wrap;
            width: 32px;
            .img{
              background: url('https://www.zymk.cn/static/images/default/share.png')no-repeat -35px 0;
              width: 27px;
              height: 32px;
            }
          }
          .three{
            height: 50px;
            display: flex;
            align-items: flex-end;
            flex-wrap: wrap;
            width: 32px;
            .img{
              background: url('https://www.zymk.cn/static/images/default/share.png')no-repeat -64px -3px;
              width: 32px;
              height: 32px;
            }
          }
        }
      }
      .van-overlay {
        background: transparent;
      }
      .lightopacity {
        .van-overlay {
          opacity: 0;
        }
        .van-popup--center {
          width: 80%;
          height: 120px;
          background: rgba(0, 0, 0, 0.7);
          display: flex;
          align-items: center;
          padding: 40px 40px 20px 40px;
          border-radius: 10px;
          display: flex;
          align-items: start;
          .chooselight {
            width: calc(100% - 90px);
            position: absolute;
            bottom: 20px;
            color: #fff;
            display: flex;
            justify-content: space-around;
            .two {
              color: #fc6976;
            }
            .one {
              display: flex;
              align-items: center;
              font-size: 18px;
              white-space: nowrap;
              i {
                padding: 0 5px;
              }
            }
          }
        }
      }
      .van-popup--right {
        background: rgba(0, 0, 0, 0.7);
        .rightlist {
          height: 100%;
          width: 100%;
          position: relative;
          color: white;
          .nowlist {
            overflow: hidden;
            height: calc(100% - 50px);
            padding-bottom: 50px;
            ul {
              overflow: scroll;
              height: 100%;
              .lightli {
                background: #fc6454;
              }
              li {
                height: 80px;
                padding: 5px 5px 20px;
                .imgs {
                  width: 100%;
                  height: 100%;
                  overflow: hidden;
                  img {
                    width: 100%;
                    margin-top: -50px;
                  }
                }
                p {
                  margin-top: 3px;
                  font-size: 10px;
                }
              }
            }
          }
          .tops {
            display: flex;
            align-items: center;
            justify-content: space-around;
            height: 50px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.2);
            .bos {
              display: flex;
              align-items: center;
              i {
                font-size: 20px;
              }
            }
          }
          .bottoms {
            width: 100%;
            height: 50px;
            position: absolute;
            left: 0;
            bottom: 0;
            display: flex;
            align-items: center;
            justify-content: space-around;
            .bos {
              display: flex;
              align-items: center;
              i {
                font-size: 20px;
              }
            }
          }
        }
      }
      .van-popup--bottom {
        color: white;
        background: rgba(0, 0, 0, 0.7);
        .fourFind {
          display: flex;
          justify-content: space-around;
          flex-wrap: wrap;
          padding-bottom: 10px;
          border-bottom: 1px solid rgba(255, 255, 255, 0.2);
          margin-bottom: 10px;
          .nowLook {
            color: #fc6454;
            border: 1px solid #fc6454 !important;
          }
          .find {
            width: 50%;
            text-align: center;
            margin: 10px 0;
            &:nth-child(3) {
              margin: 0;
            }
            &:nth-child(4) {
              margin: 0;
            }
            p {
              padding: 5px 10px;
              border: 1px solid white;
              display: inline-block;
              border-radius: 5px;
            }
          }
        }
        .p {
          display: flex;
          justify-content: center;
          width: 100%;
          font-size: 14px;
        }
      }
      .selectLook {
        .van-overlay {
          background: rgba(0, 0, 0, 0.7);
        }
        .van-popup--center {
          width: 50%;
          border-radius: 10px;
        }
      }
    }
    .topnav {
      position: fixed;
      top: 0;
      left: 0;
      display: flex;
      width: 100%;
      height: 50px;
      background-color: rgba(0, 0, 0, 0.7);
      justify-content: space-between;
      color: white;
      align-items: center;
      padding: 0 20px;
      .van-popover__wrapper {
        .van-button__content {
          span {
            font-weight: 700;
            font-size: 20px;
          }
